"use strict";

require("phoenix_html");

var handlebars = require("handlebars"); // Brunch automatically concatenates all files in your
// watched paths. Those paths can be configured at
// config.paths.watched in "brunch-config.js".
//
// However, those files will only be executed if
// explicitly imported. The only exception are files
// in vendor, which are never wrapped in imports and
// therefore are always executed.

// Import dependencies
//
// If you no longer want to use a dependency, remember
// to also remove its path from "config.paths.watched".


$(function () {
  if (!$("#likes-template").length > 0) {
    // Wrong page.
    return;
  }

  var tt = $($("#likes-template")[0]);
  var code = tt.html();
  var tmpl = handlebars.compile(code);

  var dd = $($("#post-likes")[0]);
  var path = dd.data('path');
  var p_id = dd.data('post_id');

  var bb = $($("#post-add-button")[0]);
  var u_id = bb.data('user-id');

  function fetch_likes() {
    function got_likes(data) {
      console.log(data);
      var html = tmpl(data);
      dd.html(html);
    }

    $.ajax({
      url: path,
      data: { post_id: p_id },
      contentType: "application/json",
      dataType: "json",
      method: "GET",
      success: got_likes
    });
  }

  function add_like() {

    var data = { like: { post_id: p_id, user_id: u_id } };

    $.ajax({
      url: path,
      data: JSON.stringify(data),
      contentType: "application/json",
      dataType: "json",
      method: "POST",
      success: fetch_likes
    });
  }

  bb.click(add_like);

  fetch_likes();
});

